@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.content {
  position: relative;
}

.header-menu {
  background-color: #3e474f;
  margin: 0px;
  padding: 1rem;
}

.menu-items {
  text-align: center;
  color: #99a4b1;
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  font-size: medium;
}

.item:hover {
  color: aliceblue;
}

@media (min-width: 40em) {
  .menu-items {
    grid-template-columns: 50% 1fr 1fr 1fr 1fr 1fr 1fr;
  }
}

.home-content {
  grid-gap: 1rem;
  gap: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-template-columns: 40% 1fr;
  padding: 50px 50px 50px 50px;
  background-color: #1d2225;
  min-height: 80vh;
}

.image-wrapper {
  position: relative;
  overflow: hidden;
  padding: 10px;
  display: grid;
}

.image-wrapper > img {
  display: block;
  width: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 10px;
}

.image-wrapper > .text-content {
  margin: 10px;
  position: absolute;
  inset: 0;
  font-size: small;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 10%;
  -webkit-text-decoration: solid;
          text-decoration: solid;
}

.image-wrapper > img,
.image-wrapper > .text-content {
  transition: 200ms ease-in-out;
}

.image-wrapper > .text-content {
  opacity: 0;
}

.image-wrapper:hover > .text-content {
  opacity: 1;
}

.home-text {
  padding: 5rem;
  font-family: "Inter", sans-serif;
  color: aliceblue;
  display: grid;
  font-size: 30px;
}

.gold {
  color: #bf9b30;
  font-size: larger;
  text-align: center;
}

.avatar {
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
}

.title {
  font-size: 28px;
  font-weight: 1000;
}

.image-wrapper > .text-content p {
  font-size: 22px;
}

.about-content {
  grid-gap: 1rem;
  gap: 1rem;
  min-height: 80vh;
  align-items: center;
  background-color: #1d2225;
}

.heading {
  text-align: center;
  padding: 2rem;
}

.about-content .heading .span-text {
  color: #fff;
  text-align: center;
  font-size: 30px;
  font-weight: 800;
}

.white {
  color: white;
}

.white {
  color: white;
}

.skills {
  min-height: 70vh;
  background: linear-gradient(to bottom, #1d2225, #3e474f);
}

.skills .heading {
  text-align: center;
  padding: 2rem;
}

.span-text {
  color: #fff;
  text-align: center;
  font-size: 30px;
  font-weight: 800;
}

.skills .container {
  background: rgba(0, 0, 22, 0.4);
  color: #fff;
  border-radius: 1rem;
  padding: 2rem;
  width: 90%;
  margin: auto;
}
.skills .container .row {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  flex-wrap: wrap;
  grid-gap: 1.8rem;
  gap: 1.8rem;
}

.bar {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 1rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  background: #000;
  transition: 0.2s;
}

.bar:hover {
  box-shadow: 0 8px 10px rgba(0, 2, 68, 0.8) !important;
  background-color: rgba(0, 0, 0, 0.9) !important;
}

.skills .container .bar .info i {
  font-size: 4rem;
}
.skills .container .bar .info span {
  font-size: 1rem;
  font-weight: 500;
  font-family: "Poppins";
  margin-left: 0.5rem;
}
.skils .container .bar .info img {
  max-width: 48px;
  max-height: 48px;
}

/* skills media queries starts*/
@media screen and (max-width: 600px) {
  .skills .container {
    padding: 0;
    margin: 0;
  }
  .skills .container .row {
    grid-template-columns: repeat(2, 1fr);
    margin: 1rem;
    padding: 2rem 0.2rem 2rem 0.2rem;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .skills .container {
    margin-top: 5px;
    width: 100%;
  }
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 1rem;
}

.info i {
  font-size: 4rem;
}
.info span {
  font-size: 1rem;
  font-weight: 500;
  font-family: "Poppins";
  margin-left: 0.5rem;
}
.info img {
  max-width: 48px;
  max-height: 48px;
}

.work {
  min-height: 90vh;
  background: linear-gradient(to bottom, #3e474f, #1d2225);
}

.white {
  color: white;
}

.contact {
  min-height: 60vh;
}

